#restartButton{
        position: fixed;
        width: 70px;
        height: 70px;
        background-color: #739eff;
        border-radius: 40px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        color: white;
        -moz-user-select: none;
         -ms-user-select: none;
             user-select: none;
        -webkit-user-select: none;
        cursor: pointer;
        opacity: 0.6;
        -webkit-transition-duration: 0.3s;
                transition-duration: 0.3s;
        top: 0px;
        font-weight: bold;
}
#restartButton:hover{
        opacity: 1;
}
.t{
        position: absolute;
        top: 122px;
        font-weight: bold;
        font-size: 22px;
        color: #802f12;
}
#jishi{
        left:250px
}
